import Vue from 'vue'
import Router from 'vue-router'

import lemonPay from '@/pages/lemonPay'

Vue.use(Router)

export default new Router({
	routes: [
	{
		path: '/',
		name: '',
		component: lemonPay
	},{
		path: '/lemonPay',
		name: 'lemonPay',
		component: lemonPay
	}
	]
})



