// session保存obj
function sessionSetObj(key, value) {
	if(!value){
		let obj = {};
		window.sessionStorage.setItem(key, JSON.stringify(obj));
	}
	window.sessionStorage.setItem(key, JSON.stringify(value));
}

// session获取 obj
function sessionGetObj(key) {
	let sessionItem = window.sessionStorage.getItem(key);
	if (sessionItem) {
		if(sessionItem === ''){
			return {}
		}
		return JSON.parse(sessionItem || '{}');
	} else {
		return {}
	}
}

//获取随机数
function getRandom(min, max) { 
	return Math.floor(Math.random()*(max-min+1)+min); 
}

// 阅读时长分钟
function readingNumToMinStr (readtime) {
	var hourString = (readtime % 3600);
	var minString = hourString / 60;
	return Math.ceil(minString)
}

// 阅读时长小时
function readingNumToHourStr (readtime) {
	var hourString = (readtime / 3600);
	return parseInt(hourString)
}

// 阅读时长小时保留两位小数
function toHourDecimalStr (readtime) {
	var hourString = (readtime / 3600).toFixed(2);
	if(hourString.substr(hourString.length-1,1) == 0){
		hourString = hourString.slice(0,hourString.length-1)
	}
	return hourString
}

// 阅读时长秒钟
function readingNumToSecondStr (readtime) {
	var minNum = (readtime / 60);
	var secondString = readtime - minNum * 60;
	return secondString
}

// 阅读时长分钟
function readingTotalNumToMinStr (readtime) {
	var minString = (readtime / 60);
	return parseInt(minString)
}

export default {
	install(Vue, options) {
		Vue.prototype.sessionSetObj = sessionSetObj;
		Vue.prototype.sessionGetObj = sessionGetObj;
		Vue.prototype.getRandom = getRandom;
		Vue.prototype.readingNumToMinStr = readingNumToMinStr;
		Vue.prototype.readingNumToHourStr = readingNumToHourStr;
		Vue.prototype.readingNumToSecondStr = readingNumToSecondStr;
		Vue.prototype.readingTotalNumToMinStr = readingTotalNumToMinStr;
		Vue.prototype.toHourDecimalStr = toHourDecimalStr;
	},

}



