<template>
  <div>
    <div class="modal" v-if="showModal">
      <div class="mask" @click="closeModal"></div>
      <div class="modal-content">
        <div v-if="isMessage">
          <p class="modal-title">订单信息确认</p>
          <p>您的订单信息为:</p>
          <p>购买商品: 小柠檬阅读器</p>
          <p>商品价格: ¥<span class="price"> {{user.money}}</span>/台</p>
          <p>收货人: {{user.name}}</p>
          <p>手机号码: {{user.phone}}</p>
          <p>收货地址: {{user.addresses}}</p>
          <div class="affirm-box">
            <button class="user-input submit-btn cancel-btn" type="button" @click="cancel">取消</button>
            <button class="user-input submit-btn affirm-btn" type="button" @click="affirm">确认付款</button>
          </div>
        </div>
        <div v-else>
          <p>{{notice}}</p>
        </div>
      </div>
    </div>
    <p class="pay-title">
      小柠檬阅读器订单信息
    </p>
    <div class="pay-box">
      <p class="title">
        购买商品: 小柠檬阅读器
      </p>
      <p class="title">
        商品价格: ¥<span class="price"> {{user.money}}</span>/台
      </p>
      <div class="img-box">
        <img class="lemon-img" src="../../static/pay/lemon.png" alt="">
      </div>
      <p class="title">
        订单信息:
      </p>
      <div class="form-box">
        <p class="user-delivering" v-if="isDelivering">小柠檬正在发货....</p>
        <input class="user-input" :class="isDelivering ? 'input-isDelivering' : ''" v-model="user.name" placeholder="请输入收货人姓名" :disabled="isDelivering">
        <input class="user-input" :class="isDelivering ? 'input-isDelivering' : ''" v-model="user.phone" placeholder="请输入手机号码" :disabled="isDelivering">
        <input class="user-input" :class="isDelivering ? 'input-isDelivering' : ''" v-model="user.addresses" placeholder="请输入收货地址" :disabled="isDelivering">
        <input class="user-input user-sale" :class="isDelivering ? 'input-isDelivering' : ''" v-model="user.code" placeholder="请输入优惠码" :disabled="isDelivering">
        <button class="user-input sale-btn" :class="isDelivering ? 'button-isDelivering' : ''" type="button" @click="checkCode" :disabled="isDelivering">验证优惠码</button>
        <p class="user-delivering" v-if="isVerify">验证成功,请完善订单信息并付款...</p>
        <button class="user-input submit-btn" :class="isDelivering ? 'button-isDelivering' : ''" type="button" @click="payment" :disabled="isDelivering">确认付款</button>
      </div>
    </div>
    <div class="foot-fill">
      <img class="logo-img" src="../../static/pay/logo.png" alt="">
    </div>
  </div>
</template>
<script>
import fetch from '../util/fetch'

export default {
  name: 'lemonTree',
  data() {
    return {
      showModal: false,
      isMessage: true,
      isDelivering: false,
      isVerify: false,
      user: {
        name: '',
        money: '998.00',
        phone: '',
        addresses: '',
        code: '',
      },
      notice: '信息输入有误,请重新输入...'
    }
  },
  beforeMount() {

  },
  mounted() {
    // this.getStudentInfo();
  },
  methods: {
    payment() {
      let flag = !this.user.name || !this.user.phone || !this.user.addresses
      if (flag) {
        this.isMessage = false
        this.notice = '信息输入有误,请重新输入...'
      } else {
        let myreg = /^[1][0-9]{10}$/;
        if (!myreg.test(this.user.phone)) {
          this.isMessage = false
          this.notice = '手机格式输入有误,请重新输入...'
        } else {
          this.isMessage = true
        }
      }
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
    },
    cancel() {
      this.showModal = false;
    },
    affirm() {
      const params = this.$route.query
      if (!params) {
        this.isMessage = false
        this.notice = '页面有误,请重试或联系客服人员...'
        return
      }
      const reqData = {
        openId: this.$route.query.openid,
        name: this.user.name,
        // money: Number(this.user.money) * 100,
        phone: this.user.phone,
        shippingAddress: this.user.addresses,
        code: this.user.code,
      }
      const that = this;
      fetch.payment(reqData).then(response => {
        const resData = response;
        // console.log('resData', resData)
        if (resData.errcode) {
          alert('付款失败1')
          that.isMessage = false
          that.notice = err.data.errmsg + ',请重试或联系客服人员...'
          return;
        }
        
        function onBridgeReady() {
          WeixinJSBridge.invoke(
            'getBrandWCPayRequest', {
              "appId": resData.appId, //公众号名称，由商户传入     
              "timeStamp": resData.timeStamp, //时间戳，自1970年以来的秒数     
              "nonceStr": resData.nonceStr, //随机串     
              "package": resData.package,
              "signType": resData.signType, //微信签名方式：     
              "paySign": resData.paySign //微信签名 
            },
            function(res) {
              let wxPayCallback = JSON.stringify(res.err_msg);
              if (wxPayCallback.indexOf("get_brand_wcpay_request:ok") != -1) {
                that.showModal = false
                that.isDelivering = true
              } else if (wxPayCallback.indexOf("get_brand_wcpay_request:cancel") != -1) {
                that.isMessage = false
                that.notice = '支付取消,请重试或联系客服人员...'
              } else {
                that.isMessage = false
                that.notice = '支付失败,请重试或联系客服人员...'
              }
            }
          );
        }
        if (typeof WeixinJSBridge == "undefined") {
          if (document.addEventListener) {
            document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
          } else if (document.attachEvent) {
            document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
            document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
          }
        } else {
          onBridgeReady();
        }
      }).catch(err => {
        that.isMessage = false
        that.notice = err.data.errmsg + ',请重试或联系客服人员...'
      });
    },
    checkCode() {
      this.showModal = true
      this.isMessage = false
      let flag = !this.user.code || this.user.code.length > 20
      if (flag) {
        this.notice = '优惠码输入有误,请重新输入...'
        return
      }
      const params = this.$route.query
      if (!params) {
        this.notice = '页面有误,请重试或联系客服人员...'
        return
      }
      const reqData = {
        code: this.user.code,
      }
      fetch.checkCode(reqData).then(response => {
        const resData = response;
        if (resData.errcode) {
          this.notice = '验证失败,请重试或联系客服人员...'
          return;
        }
        this.notice = '验证成功,请完善订单信息并付款...'
        setTimeout(() => {
          this.showModal = false
        }, 1000)
        this.isVerify = true
        if(this.user.money == "998.00"){
          let money = (Number(this.user.money) * 100) - resData.off
          this.user.money = (money / 100).toFixed(2)
        }
      }).catch(err => {
        this.isMessage = false
        this.notice = '验证失败,请重试或联系客服人员...'
      });
    }
  }
}
//

</script>
<style lang="less" scoped>
.foot-fill {
  height: 100%;
  width: 100%;
  background-color: #eee;
  position: fixed;
  top: 0;
  z-index: -10;
}

.modal {
  position: relative;
  .modal-content {
    position: fixed;
    box-sizing: border-box;
    padding: 20px;
    width: 80%;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    background: #fff;
    z-index: 50002;
    p {
      line-height: 28px;
      text-align: left;
    }
    .modal-title {
      font-size: 1.3rem;
      text-align: center;
      margin-bottom: 10px;
    }
    .affirm-box {
      margin-top: 10px;
      display: flex;
      justify-content: space-around;
    }
  }
  .mask {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 50001;
    background: rgba(0, 0, 0, .5);
  }
}

.logo-img {
  width: 100%;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  opacity: 0.3;
}

.pay-title {
  height: 40px;
  line-height: 40px;
  font-size: 1.2rem;
  letter-spacing: 1px;
  color: #fff;
  background-color: #459ae9;
}

.pay-box {
  width: 90%;
  margin: 0 auto;
}

.title {
  box-sizing: border-box;
  padding-left: 10px;
  text-align: left;
  margin: 10px auto;
}

.img-box {
  width: 100%;
  margin: 0 auto;
  border: 1px solid #bbb;
  border-radius: 5px;
  background-color: rgba(225, 225, 225, .5)
}

.lemon-img {
  width: 170px;
  margin: 10px auto;
}

.price {
  font-size: 1.5rem;
  color: red;
}

.user-delivering {
  color: red;
  margin-bottom: 10px;
}

.user-input {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: .8em;
  padding-left: 10px;
  margin-bottom: 10px;
  outline: none;
  border: 1px solid #bbb;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: rgba(225, 225, 225, .5)
}

.user-sale {
  width: 70%;
}

.sale-btn {
  width: 28%;
  color: #fff;
  border: none;
  background-color: rgba(28, 89, 213, .5);
}

.submit-btn {
  color: #fff;
  border: none;
  background-color: rgba(128, 90, 233, .5);
}

.cancel-btn,
.affirm-btn {
  width: 30%;
}

.cancel-btn {
  background-color: rgba(255, 48, 48, .5);
}

.input-isDelivering {
  color: rgba(249,172,43, .8);
}

.button-isDelivering {
  color: rgba(225,225,225, .8);
  background-color: rgba(128, 90, 233, .4);
}

</style>



